import * as React from "react";
import Layout from "../../components/layout";
import HeroAnimation from "../../components/home/heroAnimation";
import zanelliImg from "../../images/Zanelli_Home.png";
import ProductDesc from "../../components/product/productDesc";
import ProductTable from "../../components/product/productTable";
import zanelliImgP1 from "../../images/zanelli/zanelli_p1.png";
import zanelliImgP2 from "../../images/zanelli/zanelli_p2.jpg";
import zanelliImgP3 from "../../images/zanelli/zanelli_p3.jpg";
import zanelliImgP4 from "../../images/zanelli/zanelli_p4.jpg";
import zanelliImgP5 from "../../images/zanelli/zanelli_p5.jpg";
import zanelliImgP6 from "../../images/zanelli/zanelli_p6.jpg";
import ProductBrandSection from "../../components/product/productBrandSection";
import ProductImgSection from "../../components/product/productImgSection";
import ProductHero from "../../components/product/productHero";
import { projectsData } from "../../data/projectsData";

const product = projectsData.zanelli;

const ZanelliPage = () => {
  return (
    <Layout pageTitle="Zanelli">
      <ProductHero elem={product} isImg={true} mediaSrc={zanelliImg} />
      {[zanelliImgP1, zanelliImgP2, zanelliImgP3].map((pImg, idx) => (
        <ProductImgSection imgSrc={pImg} />
      ))}
      <ProductBrandSection imgSrc={zanelliImgP4} brand={product.brand} />
      {[zanelliImgP5, zanelliImgP6].map((pImg, idx) => (
        <ProductImgSection imgSrc={pImg} />
      ))}
    </Layout>
  );
};

export default ZanelliPage;

export const Head = () => <title>Lane7 - Zanelli</title>;
